.login {
  &-page {
    &__wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: column;

      background-color: #002D58;

      min-height: 100vh;
      padding: 100px 0 100px 0;
    }

    &__img {
      height: 215px;
      margin-bottom: 55px;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }

    &__sign-in {
      width: 300px;
    }

    &__back {
      width: 200px;
      margin: 0 auto;
    }

    &__resend {
      margin: 60px auto;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;

      font-size: 22px;
      line-height: 26px;

      text-align: center;

      color: #BEBEBE;
    }

    &__content {
      padding: 0 15px;
      max-width: 460px;
      width: 100%;
    }

    &__footer {
      margin-top: 100px;
    }

    &__link {
      margin-top: 40px;
      display: block;
      text-align: center;

      font-weight: 500;
      font-size: 16px;
      letter-spacing: -0.3px;
      text-decoration-line: underline;

      color: #329DFF;
    }
  }

  &-form {
    &__input + &__input {
      margin-top: 20px;
    }

    &__link {
      position: absolute;
      top: 50%;
      margin-top: -12px;
      right: 20px;

      font-weight: 500;
      font-size: 20px;
      line-height: 24px;
      letter-spacing: -0.3px;
      text-decoration-line: underline;

      color: #329DFF;

    }

    &__input {
      position: relative;

      &-item {
        height: 64px;
        width: 100%;
        padding: 20px;

        background: #114179;
        border: 2px solid #297BC7;
        border-radius: 10px;

        font-size: 20px;
        letter-spacing: -0.3px;
        color: #FFFFFF;
        outline: none;

        &::placeholder {
          opacity: 1;
        }
      }

      &-forgot {
        text-align: center;
        font-size: 48px;
      }
    }

    &__submit {
      margin-top: 55px;
    }

    &__content {
      max-width: 200px;
      width: 100%;
      margin: 0 auto;
    }

    &__text {
      color: #fff;
      font-weight: 500;
      font-size: 22px;
      text-align: center;

      margin-bottom: 60px;
    }
  }

}

.resend-code{
  &__time{
    font-weight: 500;
  }
  &__link{
    margin-left: 20px;
    text-decoration-line: underline;
    font-weight: 400;
    pointer-events: none;
  }
  &.active{
    .resend-code{
      &__link{
        color: #32C5FF;
        pointer-events: auto;
      }
    }
  }
}