*{
  box-sizing: border-box;
}

body{
  margin: 0;
  padding: 0;

  font-family: 'Roboto',sans-serif;
}

.btn{
  -webkit-appearance: none;
  outline: none;
  border: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 64px;

  font-size: 24px;
  font-weight: 700;
  letter-spacing: -0.3px;
  text-transform: uppercase;

  color: #FFFFFF;

  &:hover{
    cursor: pointer;
  }

  &-yellow{
    background: linear-gradient(111.04deg, #FFD900 -19.61%, #E86F00 94.09%);
    box-shadow: 0px 32px 20px -20px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
  }
  &-blue{
    background: linear-gradient(111.04deg, #32C5FF -19.61%, #1E5BA4 94.09%);
    box-shadow: 0px 32px 20px -20px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
  }
}


.page-wrapper{
  &__content{
    display: flex;
    align-items: stretch;
    min-height: calc(100vh - 70px);
  }
}

.page-content{
  width: 100%;
  &__wrapper{
    background-color:#002D58;
    width: 100%;
    height: 100%;

    padding: 48px 33px;
  }
}

.page-block{
  &__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title{
    font-size: 44px;
    font-weight: 700;
    color: #FFFFFF;
  }
  &__content{
    margin-top: 44px;
  }
}

.currency-select{
  background: none;
  outline: none;
  border: none;

  font-size: 34px;
  font-weight: 500;
  color:#fff;
}

.trigger{
  &.active{
    .trigger-block{
      display: block;
    }
  }
  &-block{
    display: none;
  }
  &-link{
    &:hover{
      cursor: pointer;
    }
  }
}

.language-select{
  background: none;
  outline: none;
  border: none;

  text-transform: uppercase;
  color:#fff;
  font-weight: 500;

  letter-spacing: -0.3px;

  font-size: 14px;
}