.left-aside{

  background-color: #14386E;

  flex-shrink: 0;
  &__wrapper{
    padding: 60px 0;
  }
}

.page-list{
  width: 320px;

  transition:.3s all;
  &.hide{
    width: 80px;
    .page-list{
      &__item{
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &__link{
        &-text,&-wrapper::after{
          display: none;
        }
      }
    }
  }
  &__item{
    padding: 0 34px;

    &_current{
      .page-list__link{
        &-icon{
          path{
            fill: #fff;
          }
        }
        &-text{
          color:#fff;
        }
      }
    }
  }
  &__item + &__item{
    margin-top: 40px;
  }
  &__link{
    &-wrapper{
      display: flex;
      align-items: center;
      position: relative;
      &::after{
        content: '';
        width: 16px;
        height: 16px;
        display: block;

        background: url("../img/white-arrow.svg") center center no-repeat;
        background-size: contain;

        margin-left: auto;
      }
    }
    &-icon{
      display: flex;
      align-items: center;
      justify-content: center;

      flex-shrink: 0;

      width: 20px;
      height: 20px;

      svg{
        width: 100%;
        height: 100%;
      }
    }
    &-text{
      margin-left: 17px;

      font-weight: 500;
      font-size: 18px;
      color: #329DFF;
    }
  }
}

@media screen and (max-width: 991px) {
  .left-aside{
    position: fixed;
    left: 0;
    top: 70px;
    height: 100vh;
    opacity: 0;
    z-index: -1;
  }
}