.wallet {
  &-block + &-block{
    margin-top: 44px;
  }
  &-block {
    &__header {
      display: flex;
      align-items: center;
    }

    &__title {
      color: #fff;
      font-size: 30px;
      font-weight: 500;
    }

    &__select {
      margin-left: 50px;

      background: none;
      border: none;
      outline: none;

      font-size: 30px;
      font-weight: 500;
      color: #297BC7;
    }

    &__help {
      margin-top: 15px;
    }

    &__content {
      margin-top: 40px;
    }
  }

  &-grid {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-auto-rows: 260px;
    gap: 30px;
  }

  &-card {
    &_empty{
      grid-column-end: span 4;
      grid-row-end: span 1;
    }
    &__wrapper {
      border: 2px solid #1E5BA4;
      border-radius: 16px;
      height: 100%;

      overflow: hidden;

      transition: .3s border-color;
      &:hover{
        border-color: #fff;
      }
    }

    &__content {
      padding: 32px 26px 28px 26px;

      display: flex;
      align-items: center;
    }

    &__info {
      width: 100%;
    }

    &__img {
      width: 60px;
      height: 60px;

      display: flex;
      align-items: center;
      justify-content: center;

      flex-shrink: 0;

      margin-right: 20px;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }

    &__header {
      display: flex;
      align-items: center;
    }

    &__name {
      font-size: 16px;
      color:#fff;
      span {
        font-weight: 700;
      }
    }

    &__address {
      display: flex;
      align-items: center;

      margin-left: 16px;

      &-select {
        background: none;
        border: none;
        outline: none;

        letter-spacing: -0.3px;

        color: #297BC7;
        font-size: 16px;
      }

      &-control {
        display: flex;
        align-items: center;
      }

      &-link {
        width: 20px;
        height: 20px;

        display: block;

        svg {
          width: 100%;
          height: 100%;
        }
      }

      &-link {
        margin-left: 10px;
      }
    }

    &__remove {
      margin-left: auto;

      &-icon {
        svg {
          path {
            transition: .3s all;
          }
        }

        &:hover {
          svg {
            path {
              fill: #FF3333;
              fill-opacity: 1;
            }
          }
        }
      }
    }

    &__count{
      &-link{
        display: flex;
        align-items: center;

        font-size: 34px;
        font-weight: 500;
        letter-spacing: -0.3px;

        color:#fff;

        margin: 8px 0 12px 0;

        &::after{
          content: '';
          width: 16px;
          height: 16px;
          display: block;

          margin-left: 10px;

          background: url("../img/white-arrow.svg") center center no-repeat;
          background-size: contain;
        }
      }
    }

    &__exchange{
      display: flex;
      align-items: center;


      &-all{
        font-size: 18px;
        color:#fff;
        letter-spacing: -.3px;
      }
      &-one{
        margin: 0 24px;
        display: block;

        font-size: 18px;

        letter-spacing: -0.3px;

        color: #297BC7;
      }
    }

    &__footer{
      display: grid;
      grid-template-columns: repeat(5,minmax(0,1fr));

      gap: 30px;

      padding: 24px;


      background: #0A2545;
      border-radius: 0px 0px 14px 14px;

      margin-top: auto;
    }

    &__btn{
      font-size: 18px;
      font-weight: 700;

      text-transform: capitalize;

      height: 50px;

      &_big{
        grid-column-end: span 2;
      }
    }
  }

  &-empty{
    height: 100%;
    &__wrapper{
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      height: 100%;
    }
    &__title{
      margin-top: 25px;
      font-weight: 500;
      font-size: 22px;
      color:#fff;
    }
    &__text{
      margin-top: 10px;
      font-size: 16px;
      color:#fff;
    }
  }
}

.trend{
  &__wrapper{
    display: flex;
    align-items: center;
  }
  &__label{
    font-size: 18px;
    letter-spacing: -0.3px;

    color: #66FF66;

    margin-left: 6px;
  }

  &_decrise{
    .trend{
      &__label{
        color: #FF3333;
      }
      &__icon{
        transform: rotate(180deg);
        svg{
          path{
            fill:#FF3333;
          }
        }
      }
    }
  }
}

.help-block {
  position: relative;
  &__wrapper {
    display: flex;
    align-items: center;
    width: fit-content;
    &:hover{
      cursor: pointer;
      .help-block__icon{
        path{
          fill:#fff;
        }
      }
    }
  }

  &__content{
    position: absolute;

    top: 30px;
    left: -50px;

    max-width: 510px;
    width: 100%;
    padding: 19px 21px;

    background: rgba(0, 0, 0, 0.7);
    border-radius: 10px;

    &::after{
      content: '';
      position: absolute;
      left: 50%; top: -20px;
      margin-left: -10px;
      border: 10px solid transparent;
      border-bottom: 10px solid rgba(0,0,0,0.7);
    }
  }

  &__text{
    font-size: 16px;
    line-height: 22px;

    color:#fff;
  }

  &__icon {
    flex-shrink: 0;
    width: 20px;
    height: 20px;

    margin-left: 22px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    font-size: 18px;
    color: #297BC7;
  }
}


@media screen and (max-width: 768px) {
  .wallet{
    &-grid{
      grid-template-columns: minmax(0,1fr);
    }
  }
}