.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  background: rgba(0, 0, 0, 0.5);

  opacity: 0;
  z-index: -1;

  transition: .5s all;

  &.active{
    opacity: 1;
    z-index: 99;
  }

  &-currency{
    &__content{
      margin-top: 60px;
    }
  }

  &__wrapper{
    max-width: 600px;
    width: 100%;

    background: #14386E;
    box-shadow: 0px 4px 90px rgba(0, 0, 0, 0.15);
    border-radius: 14px;

    padding: 35px;
  }

  &__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__title{
    font-size: 30px;
    font-weight: 700;
    color:#fff;
  }

  &__close{
    display: flex;
    align-items: center;
    justify-content: center;
  }
}


.currency-list{
  &__item{
    background: #002D58;
    border-radius: 14px;

    padding: 15px;
  }

  &__item + &__item{
    margin-top: 18px;
  }

  &__link{
    display: flex;
    align-items: center;
    &::after{
      content: '';
      width: 15px;
      height: 15px;
      display: block;

      margin-left: auto;

      background: url("../img/white-arrow.svg") center center no-repeat;
      background-size: contain;
    }
    &-icon{
      height: 40px;
      width: 40px;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &-label{
      margin-left: 15px;
      font-size: 22px;

      color:#fff;
      span{
        font-weight: 700;
      }
    }
  }

}